@import '~@/styles/mixin.scss';
@import '~@/styles/var.scss';

.search-list {
  position: relative;
  height: 100%;
  & &-tab {
    display: flex;
    padding-bottom: 20px;
    line-height: 100px;
    color: rgba(255, 255, 255, 0.5);
    background-color: $theme-bg;
    li {
      flex: 1;
      text-align: center;
      span {
        padding-bottom: 15px;
      }
      &.active span {
        font-weight: 700;
        color: #fff;
        border-bottom: 6px solid #fff;
      }
    }
  }
  & &-content {
    position: absolute;
    top: 120px;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    .search-content-item {
        padding-top: 20px;
        height: 100%;
      display: none;
      &.active {
        display: block;
      }
    }
  }
}
